/* unplugin-vue-components disabled */import { computed, onMounted, reactive, ref, watch, nextTick } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
export default {
  name: 'Home',

  setup() {
    const input = ref('');
    const dialogVisible = ref(false);
    const refInput = ref(null); // 已存在用户

    const usersInfo = reactive([{
      allowDelete: false,
      id: 1,
      ischecked: false,
      name: '张三',
      age: 18,
      gender: '男',
      phone: 'xxxxx',
      address: 'xxxxxxxxx'
    }]);
    const usersInfoCopy = reactive([]);
    const id = computed(() => usersInfo.length);
    const currentId = ref(-1); // 校验规则

    const rules = reactive({
      name: [{
        required: true,
        message: '请输入姓名',
        trigger: 'blur'
      }, {
        min: 2,
        max: 8,
        message: '请输入2-8个汉字',
        trigger: 'blur'
      }],
      gender: [{
        required: true,
        message: '请选择性别',
        trigger: 'change'
      }],
      phone: [{
        required: true,
        message: '请输入电话(11位数字)',
        trigger: 'blur'
      }, {
        type: 'number',
        message: '请输入电话(11位数字)'
      }],
      detailAddress: [{
        required: true,
        message: '请输入地址',
        trigger: 'blur'
      }]
    });
    const formRef = ref(null); // 新建用户

    const form = reactive({
      name: '',
      gender: '',
      phone: '',
      age: '18',
      address: '',
      detailAddress: ''
    }); // 聚焦失焦时触发

    function focus() {
      input.value = input.value === '按关键字搜索' ? '' : input.value;
    }

    function blur() {
      nextTick(() => {
        refInput.value.blur();
      });
      input.value = input.value === '' ? '按关键字搜索' : input.value; // 不用过滤

      if (input.value === '按关键字搜索') {
        usersInfoCopy.splice(0);

        for (let i = 0; i < usersInfo.length; i++) {
          usersInfoCopy.push(usersInfo[i]);
        }
      } else {
        // 过滤后
        usersInfoCopy.splice(0);
        usersInfo.forEach(eachUser => {
          if (eachUser.name.indexOf(input.value) != -1 || eachUser.gender.indexOf(input.value) != -1 || String(eachUser.age).indexOf(input.value) != -1 || eachUser.address.indexOf(input.value) != -1) {
            usersInfoCopy.push(eachUser);
          }
        });

        if (usersInfoCopy.length === 0) {
          usersInfoCopy.push(usersInfo[0]);
        }
      }
    } // 关闭表单触发


    function handleClose() {
      ElMessageBox.confirm('信息尚未保存，是否确认关闭?', '提示', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        dialogVisible.value = false;
        ElMessage({
          type: 'info',
          message: '已取消保存'
        });
      }).catch(() => {});
    } // 删除用户信息


    function deleteUser(userid) {
      const findUserIndex = usersInfo.findIndex(eachUser => {
        return eachUser.id === userid;
      });

      if (findUserIndex == -1) {
        return;
      }

      if (usersInfo[findUserIndex].allowDelete === false) {
        return ElNotification({
          title: '警告',
          message: '权限不够，禁止删除!',
          type: 'warning',
          duration: 1000
        });
      }

      setOldItem(JSON.stringify(usersInfo));
      usersInfo.splice(findUserIndex, 1);
      resetId();
      setNewItem(JSON.stringify(usersInfo));
    } // 批量删除


    function deleteMore() {
      let idArr = [];
      usersInfo.forEach(eachUser => {
        if (eachUser.ischecked) {
          idArr.push(eachUser.id);
        }
      });
      const oldUsersInfo = JSON.stringify(usersInfo);

      for (let i = 0; i < idArr.length; i++) {
        const findUserIndex = usersInfo.findIndex(eachUser => {
          return eachUser.id === idArr[i];
        }); // 带权限的不允许删除

        if (usersInfo[findUserIndex].allowDelete === false) {
          return ElNotification({
            title: '警告',
            message: '权限不够，禁止删除张三!',
            type: 'warning',
            duration: 1000
          });
        }

        usersInfo.splice(findUserIndex, 1);
      }

      setOldItem(oldUsersInfo);
      resetId();
      setNewItem(JSON.stringify(usersInfo));
    } // 重新排序id


    function resetId() {
      let newId = 1;
      usersInfo.forEach((eachUser, index) => {
        usersInfo[index].id = newId;
        newId++;
      });
    } // 撤销


    function layout() {
      const oldUsersInfo = JSON.parse(localStorage.getItem('OLDUSERSINFO')); // 获取旧值后将现在的值作为旧值

      setOldItem(JSON.stringify(usersInfo));
      usersInfo.splice(0);

      for (let i = 0; i < oldUsersInfo.length; i++) {
        usersInfo.push(oldUsersInfo[i]);
      } // 撤销后将旧值作为新值


      setNewItem(JSON.stringify(usersInfo));
    } // 保存表单


    async function saveUsers() {
      if (!formRef) return;

      try {
        formRef.value.validate(valid => {
          // 验证通过
          if (valid) {
            // 筛选当前修改的用户对象
            const findUserIndex = usersInfo.findIndex(eachUser => {
              return eachUser.id === currentId.value;
            }); // 新增用户

            if (findUserIndex == -1) {
              const newUser = {};
              newUser.id = id.value;
              newUser.ischecked = false;
              newUser.name = form.name;
              newUser.gender = form.gender;
              newUser.age = form.age;
              newUser.phone = form.phone;
              newUser.address = form.address + form.detailAddress; // 过滤完全相同信息

              let isInclude = filters(newUser);

              if (isInclude) {
                return ElMessage('相同信息已存在');
              }

              newUser.id = id.value + 1; // 即使缓存新旧值

              setOldItem(JSON.stringify(usersInfo));
              usersInfo.push(newUser);
              setNewItem(JSON.stringify(usersInfo));
            } // 编辑用户
            else {
              setOldItem(JSON.stringify(usersInfo));
              usersInfo[findUserIndex].name = form.name;
              usersInfo[findUserIndex].gender = form.gender;
              usersInfo[findUserIndex].phone = form.phone;
              usersInfo[findUserIndex].address = form.address + form.detailAddress;
              setNewItem(JSON.stringify(usersInfo));
            } // 保存时关闭弹窗，当前id重置


            dialogVisible.value = false;
            currentId.value = -1;
          } else {
            return false;
          }
        });
      } catch (error) {
        return false;
      }
    } // 新增或编辑


    function changeUsers(userid) {
      if (userid) {
        const findUserIndex = usersInfo.findIndex(eachUser => {
          return eachUser.id === userid;
        });
        currentId.value = userid;
        form.name = usersInfo[findUserIndex].name;
        form.gender = usersInfo[findUserIndex].gender;
        form.phone = usersInfo[findUserIndex].phone;
        form.detailAddress = usersInfo[findUserIndex].address;
        dialogVisible.value = true;
      } else {
        // 没有id则清空form
        form.name = '';
        form.gender = '';
        form.phone = '';
        form.detailAddress = '';
        dialogVisible.value = true;
      }
    } // 筛选重复函数


    function filters(newUser) {
      let result = false;
      usersInfo.forEach(eachUser => {
        if (eachUser.name === newUser.name && eachUser.address === newUser.address && eachUser.phone === newUser.phone) {
          result = true;
        }
      });
      return result;
    } // 本地会话存储新值


    function setNewItem(newItem) {
      localStorage.setItem('NEWUSERSINFO', newItem);
    } // 本地会话存储旧值


    function setOldItem(oldItem) {
      localStorage.setItem('OLDUSERSINFO', oldItem);
    } // 联级选择触发事件


    const hover = {
      expandTrigger: 'hover'
    }; // 选择城市地址后进行转化

    const handleChange = value => {
      let str = '';
      value.forEach(each => {
        str += each;
      });
      form.address = str;
    };

    const options = [{
      value: '广东省',
      label: '广东省',
      children: [{
        value: '广州市',
        label: '广州市',
        children: [{
          value: '番禺区',
          label: '番禺区'
        }, {
          value: '越秀区',
          label: '越秀区'
        }, {
          value: '海珠区',
          label: '海珠区'
        }, {
          value: '天河区',
          label: '天河区'
        }, {
          value: '白云区',
          label: '白云区'
        }, {
          value: '荔湾区',
          label: '荔湾区'
        }]
      }]
    }]; // 即使更新复制的用户信息

    watch(usersInfo, () => {
      blur();
    });
    onMounted(() => {
      setOldItem(JSON.stringify(usersInfo));

      if (localStorage.getItem('NEWUSERSINFO')) {
        const oldUsersInfo = JSON.parse(localStorage.getItem('NEWUSERSINFO'));
        usersInfo.splice(0);

        for (let i = 0; i < oldUsersInfo.length; i++) {
          usersInfo.push(oldUsersInfo[i]);
        }
      }

      setNewItem(JSON.stringify(usersInfo));
      blur();
    });
    return {
      input,
      usersInfo,
      dialogVisible,
      form,
      hover,
      options,
      id,
      currentId,
      rules,
      formRef,
      usersInfoCopy,
      refInput,
      focus,
      blur,
      handleClose,
      deleteMore,
      handleChange,
      changeUsers,
      saveUsers,
      deleteUser,
      layout
    };
  }

};